export default {
  props: {
    list: {
      type: Array,
      default: []
    }
  },
  data () {
    return {
      userId: ''
    }
  },
  created () {
    this.userId = this.$route.params.userId
    console.log(this.userId)
  },
  methods: {
    changeStatus (num) {
      switch (num) {
        case 0:
          return '待报价'
        case 1:
          return '已撤销'
        case 2:
          return '待承保'
        case 3:
          return '待付款'
        case 4:
          return '已承保'
        case 5:
          return '核保失败'
      }
    },
    changePrice (item) {
      switch (item.orderStatus) {
        case 0:
          return '待报价'
        case 1:
          return '0.00'
        case 2:
          return '￥' + item.maxFee
        case 3:
          return '￥' + item.maxFee
        case 4:
          return '￥' + item.maxFee
        case 5:
          return '暂无保费'
      }
    },
    handleSaveData (item) {
      this.$localStorage.set('orderDetail', JSON.stringify(item))
    }
  }
}



